import React from 'react';
import styled from '@emotion/styled';
import Img from 'gatsby-image';

const ArticleContainer = styled.article`
	display: flex;
	height: 5rem;
	transition: ${props => props.theme.transitions.boom.transition};
	overflow: hidden;
	background: ${props => props.theme.colors.white.base};
	box-shadow: ${props => props.theme.shadow.feature.small.default};
	&:hover {
		box-shadow: ${props => props.theme.shadow.feature.small.hover};
		transform: scale(1.01);
	}
	@media (max-width: 1100px) {
		height: 7.5rem;
	}
	@media (max-width: 600px) {
		height: 5rem;
	}
`;

const ArticleImgCol = styled.div`
	display: flex;
	width: 25%;
	position: relative;
`;

const ArticleImg = styled.div`
	width: 100%;
	height: 100%;
	position: relative;
`;

const ArticleContentCol = styled.div`
	display: flex;
	width: 75%;
	display: flex;
	align-items: center;
	padding: 0 1rem 0;
	@media (max-width: 1100px) {
		align-items: flex-start;
	}
`;

const ArticleContent = styled.div`
	width: 100%;
	padding: 1rem 0;
`;

const ArticleTitle = styled.h4`
	margin: 0;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
`;

const ArticleDetail = styled.p`
	text-decoration: none;
	margin: 0;
	font-size: 0.85rem;
	line-height: 1.25;
`;

const ArticleListItem = ({ article }) => (
	<li>
		<a href={article.mediumUrl}>
			<ArticleContainer>
				<ArticleImgCol>
					<ArticleImg aria-hidden={'true'}>
						<Img
							fluid={article.imageUrl.childImageSharp.fluid}
							style={{
								position: 'static',
								width: '100%',
							}}
						/>
					</ArticleImg>
				</ArticleImgCol>
				<ArticleContentCol>
					<ArticleContent>
						<ArticleTitle>{article.title}</ArticleTitle>
						<ArticleDetail>{article.publishedDate}</ArticleDetail>
						<ArticleDetail>by {article.author}</ArticleDetail>
					</ArticleContent>
				</ArticleContentCol>
			</ArticleContainer>
		</a>
	</li>
);

export default ArticleListItem;
